import * as React from 'react';
import { gsap } from 'gsap';
import { Link } from 'gatsby';
import Layout from '../components/layout/layout';
import { container, intro } from './home.layout.css';

// markup
// const IndexPage = () => (
//   <div className="app">
//     <Home />
//   </div>
// );

const IndexPage = () => {
  const introEl = React.useRef();
  const introTl = React.useRef();
  const introText = React.useRef();
  const introTextSelector = gsap.utils.selector(introText);
  const introLinks = React.useRef();

  React.useLayoutEffect(() => {
    introTl.current = gsap
      .timeline()
      .from(introEl.current, { opacity: 0, duration: 0.5 })
      .from(introTextSelector('.introText'), {
        opacity: 0,
        scale: 0,
        ease: 'back',
      })
      .from(introLinks.current, { opacity: 0, scale: 0, ease: 'back' });

    return () => {
      introTl.current.kill();
    };
  }, []);

  return (
    <Layout currentPage="Home" pageTitle="Home">
      <div className="home-container">
        <div className="intro" ref={introEl}>
          <div ref={introText}>
            <div style={{ fontSize: '1.1vw' }} className="introText">
              Hi, I'm
            </div>
            <h1 className="introText">
              Atharv
              <br />
              Kulkarni
            </h1>
          </div>

          <div className="resource-linking" ref={introLinks}>
            You can check out all my <Link to="/projects">Projects</Link> <br />
            or read the <Link to="/blog">articles I've written</Link>.
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default IndexPage;
